import 'ol/ol.css';
import{Map, View} from 'ol/';
import TileWMS from 'ol/source/TileWMS';
import TileLayer from 'ol/layer/Tile';
import { Image as ImageLayer } from 'ol/layer' 
import Projection from 'ol/proj/Projection';
import ImageWMS from 'ol/source/ImageWMS';


const myProjection=new Projection({
    code:'EPSG:32643',
   units:'m',
   axisOrientation:'neu',
   global:false
 });
const orthosrc=new TileWMS({
    url:"http://ec2-13-235-246-32.ap-south-1.compute.amazonaws.com:8080/geoserver/Curtorim/wms",
    params:{"LAYERS":"Curtorim:curtorim_cog" , "VERSION":"1.1.1" , "FORMAT":"image/jpeg"}
});
const ortholayer=new TileLayer({
    source:orthosrc
})
const hnadikurisrc=new ImageWMS({
    url:"http://ec2-13-235-246-32.ap-south-1.compute.amazonaws.com:8080/geoserver/Curtorim/wms",
    params:{"LAYERS":"Curtorim:curtorim-lines" , "VERSION":"1.1.1" , "FORMAT":"image/png"}
});
const handikuris=new ImageLayer({
    source:hnadikurisrc
})
 
 const view=new View({
    center:[393615.64087,1691759.88130],
    zoom:0,
    extent:[392482.26872767013,1690868.0004283397,394920.8374327267,1692592.5770503664],
    projection:myProjection
});
const map=new Map({
    target:"map",
    layers:[ortholayer,handikuris],
    view:view

})
